import { getRandomNumber } from './getRandomNumber';
export const getRandomColor = () => {
    const colorArr = [
        getRandomNumber(16).toString(16),
        getRandomNumber(16).toString(16),
        getRandomNumber(16).toString(16),
        getRandomNumber(16).toString(16),
        getRandomNumber(16).toString(16),
        getRandomNumber(16).toString(16),
    ];
    return `#${colorArr.join('')}`;
};
